/*

font-family: 'Black Han Sans', sans-serif;
font-family: 'Noto Sans KR', sans-serif;
*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Noto Sans KR", "Black Han Sans", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 10px;
}

html,
body {
  font-size: 10px !important;
  z-index: -1;
}

body {
  height: 100%;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #000;
  outline: none;
  cursor: pointer;
}

button {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  background: transparent;
  border: none;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}

input[type="button"] {
  cursor: pointer;
}

input[type="text"],
input[type="password"],
input[type="email"],
input[type="search"],
input[type="image"],
input[type="tel"],
input[type="button"],
input[type="submit"],
textarea {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  outline: none;
}

input[type="text"]::placeholder,
input[type="tel"]::placeholder {
  color: #868d96;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin: 0 auto;
}

select {
  /* -webkit-appearance: none; */
  /* -moz-appearance: none; */
  /* appearance: none; */
  outline: none;
  z-index: 99;
}

select::-ms-expand {
  display: none;
}

/* align */

.align-left {
  text-align: left !important;
}

.align-center {
  text-align: center !important;
}

.align-right {
  text-align: right !important;
}

.align-middle {
  vertical-align: middle !important;
}

.align-top {
  vertical-align: top !important;
}

.align-bottom {
  vertical-align: bottom !important;
}

/* display */

.block {
  display: block;
}

.in-block {
  display: inline-block;
}

/* width */

