}

/* font-style */

.font-normal {
  font-weight: normal;
}

.font-bold {
  font-weight: bold;
}

.font-0 {
  font-size: 0;
}

.bg-img {
  display: inline-block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.clear {
  clear: both;
}
